/* These are not typos. They are being used to swap the font without touching other legacy font declarations */ 
@font-face {
    font-family: roboto slab;
    font-style: normal;
    font-weight: normal;
    font-display: swap;
    src: url('fonts/knockout/Knockout-50Welterweight.woff2') format('woff2');
    font-feature-settings: "ss02";
  }
  
  /* dm-sans-regular - latin_latin-ext */
  @font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: roboto;
    font-style: normal;
    font-weight: 400;
    src: url('fonts/dm-sans/dm-sans-v11-latin_latin-ext-regular.woff2') format('woff2'), /* Chrome 36+, Opera 23+, Firefox 39+ */
         url('fonts/dm-sans/dm-sans-v11-latin_latin-ext-regular.woff') format('woff'); /* Chrome 5+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }
  /* dm-sans-italic - latin_latin-ext */
